                },
            ],
            textDataList:[]
        };
    },
    mounted() {
        this.requestTextData()
    },
    methods: {
        requestTextData() {
            let params = {
                type: this.bgmId
            }
            http.get('/hotfocus/hotFocusQueryPage').then(res => {
                this.textDataList = res.data.result;
            })
        },
        bgmChange(val) {
            this.bgmId = val.value;
            this.requestTextData();
        }
    }
    
    
};
</script>

